.layout {
  min-height: 100vh;
  position: absolute;
  width: 100%;
}

.background {
  position: fixed;
  z-index: -2;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  top: 0;
  left: 0;
}

.bgHidden {
  transition: opacity 1s ease;
  opacity: 0;
  z-index: -1;
}

.header {
  position: fixed;
  width: 100%;
  max-width: 1080px;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buttonGroup {
  width: 100%;
  justify-content: center;
}

.button {
  padding: 10px 40px !important;
}

.slider {
  width: 90% !important;
}

.childrenMobile {
  padding: 15% 2% 0 2%;
}

.footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 20px;
  color: white;
}

.avatar {
  width: 50px !important;
  height: 50px !important;
  background: rgb(229, 229, 229) !important;
  cursor: pointer;
  margin: 5%;
  &:hover {
    background: white !important;
  }
}

.icon {
  font-size: 2em !important;
}
